import React from 'react';
const context = React.createContext(undefined);
export const { Consumer } = context;
export class SegmentPositionProvider extends React.PureComponent {
    constructor() {
        super(...arguments);
        this.state = {
            active: null,
            register: this.register.bind(this),
            unregister: this.unregister.bind(this),
            scrollToId: this.scrollToId.bind(this),
            registry: new Map(),
        };
        this.refMap = new Map();
    }
    componentDidUpdate(_prevProps, prevState) {
        const { registry } = this.state;
        if (registry !== prevState.registry) {
            const toCheck = Array.from(registry.keys()).sort((a, b) => a - b);
            const activeId = toCheck.find(id => registry.get(id));
            const active = activeId !== undefined ? activeId : (toCheck.length > 0 ? toCheck[toCheck.length - 1] : null);
            if (active !== this.state.active) {
                this.setState({ active });
            }
        }
    }
    render() {
        return <context.Provider value={this.state}>{this.props.children}</context.Provider>;
    }
    register(scrollable, id, state) {
        if (this.state.registry.get(id) === state) {
            return;
        }
        this.refMap.set(id, scrollable);
        this.setState({
            registry: new Map(this.state.registry).set(id, state),
        });
    }
    unregister(id) {
        if (!this.state.registry.has(id)) {
            return;
        }
        this.refMap.delete(id);
        const registry = new Map(this.state.registry);
        registry.delete(id);
        this.setState({ registry });
    }
    scrollToId(id = this.state.active || undefined) {
        if (id === undefined) {
            return;
        }
        const ref = this.refMap.get(id);
        if (ref === undefined) {
            return;
        }
        ref.scrollIntoView();
    }
}
