import { t } from '@lingui/macro';
import Module, { DISPLAY_MODE } from 'parser/core/Module';
import React from 'react';
import DISPLAY_ORDER from '../DISPLAY_ORDER';
import { StatisticComponent } from './StatisticComponent';
import styles from './Statistics.module.css';
export class Statistics extends Module {
    constructor() {
        super(...arguments);
        this.statistics = [];
    }
    add(statistic) {
        this.statistics.push(statistic);
    }
    output() {
        if (!this.statistics.length) {
            return false;
        }
        return (<div className={styles.statistics}>
				{this.statistics.map((statistic, i) => (<StatisticComponent key={i} statistic={statistic}/>))}
			</div>);
    }
}
Statistics.handle = 'statistics';
// tslint:disable-next-line:variable-name
Statistics.title = t('core.statistics.title') `Statistics`;
Statistics.displayOrder = DISPLAY_ORDER.STATISTICS;
Statistics.displayMode = DISPLAY_MODE.RAW;
