import classnames from 'classnames';
import NormalisedMessage from 'components/ui/NormalisedMessage';
import React from 'react';
import styles from './SegmentLinkItem.module.css';
import { Consumer } from './SegmentPositionContext';
export default function SegmentLinkItem({ result, index }) {
    return <Consumer>{({ active, scrollToId }) => (<div className={classnames(styles.link, active === index && styles.active)} onClick={() => scrollToId(index)}>
			<NormalisedMessage message={result.name} id={result.i18n_id}/>
		</div>)}</Consumer>;
}
