import classNames from 'classnames';
import React from 'react';
import { Icon } from 'semantic-ui-react';
import styles from './Message.module.css';
import { MessageHeader } from './MessageHeader';
const typePrecedence = [
    'error',
    'warning',
    'info',
    'success',
];
export class Message extends React.PureComponent {
    render() {
        const { icon, children } = this.props;
        // Find the first message type that's truthy
        const type = typePrecedence.find(prop => this.props[prop] === true);
        return (<div className={classNames(styles.message, type && styles[type])}>
				{icon && <Icon className={styles.icon} name={icon}/>}
				<div>
					{children}
				</div>
			</div>);
    }
}
Message.Header = MessageHeader;
