import ExtendableError from 'es6-error';
export const SEVERITY = {
    ERROR: 'error',
    WARNING: 'warning',
};
// Base error handling
export class BaseError extends ExtendableError {
    constructor(options) {
        super();
        this.severity = SEVERITY.ERROR;
        Object.keys(options || {}).forEach(key => {
            this[key] = options[key];
        });
    }
}
// Module broken due to a dependency being broken
export class DependencyCascadeError extends BaseError {
    constructor(options) {
        super(Object.assign({
            dependency: '[Not Specified]',
        }, options));
        this.message = 'Dependency error cascade.';
    }
    get detail() {
        return `The ${this.dependency} module, which this module depends on, has failed.`;
    }
}
// Global Errors
export class GlobalError extends BaseError {
}
// TODO: Should I care about not being able to override the message?
export class ReportNotFoundError extends GlobalError {
    constructor() {
        super(...arguments);
        this.severity = SEVERITY.WARNING;
        this.message = 'Report not found.';
        this.detail = 'The report specified either does not exist, or is private. Make sure you pasted the correct URL, and your log is either public or unlisted.';
    }
}
// TODO: Bit of repetition. Should these be combined, or...?
export class NotFoundError extends GlobalError {
    constructor(options) {
        super(Object.assign({
            type: '[Not Specified]',
        }, options));
        this.severity = SEVERITY.WARNING;
        this.message = 'Not found.';
    }
    get detail() {
        return `No ${this.type} was found with the specified ID. If this report was generated by live logging, it may have been cached - try pressing Refresh to reload it.`;
    }
}
export class DidNotParticipateError extends GlobalError {
    constructor(options) {
        super(Object.assign({
            combatant: '[Not Found]',
            fight: 0,
        }, options));
        this.severity = SEVERITY.WARNING;
        this.message = 'Didn\'t participate.';
    }
    get detail() {
        return `Combatant ${this.combatant} did not take part in fight ${this.fight}.`;
    }
}
export class UnknownApiError extends GlobalError {
    constructor() {
        super(...arguments);
        this.message = 'API Error.';
        this.detail = 'An error occured while requesting data from FFLogs. If this issue persists, let us know on Discord.';
    }
}
export class ModulesNotFoundError extends GlobalError {
    constructor() {
        super(...arguments);
        this.message = 'Modules not found.';
        this.detail = 'A new version has probably been deployed. If refreshing the page does not fix this error, please let us know on Discord!';
    }
}
export class ReportProcessingError extends GlobalError {
    constructor() {
        super(...arguments);
        this.message = 'Report is still processing.';
        this.detail = 'FF Logs takes a brief period to process reports before xivanalysis is able to analayse them. Try again shortly!';
    }
}
