import { merge } from 'lodash';
import React from 'react';
import { Pie } from 'react-chartjs-2';
import { AbstractStatistic } from './AbstractStatistic';
import styles from './PieChartStatistic.module.css';
const DEFAULT_PIE_STAT_WIDTH = 2;
const CHART_SIZE = 80;
const DEFAULT_CHART_OPTIONS = {
    responsive: false,
    legend: { display: false },
    tooltips: { enabled: false },
};
const MISSING_COLOUR_FALLBACK = '#888';
export class PieChartStatistic extends AbstractStatistic {
    constructor(opts) {
        super({
            ...opts,
            width: opts.width || DEFAULT_PIE_STAT_WIDTH,
        });
        this.Content = () => <>
		<div className={styles.chartWrapper}>
			<Pie width={CHART_SIZE} height={CHART_SIZE} data={this.chartData} options={this.options}/>
		</div>
		<table className={styles.legend}>
			<thead>
				<tr>
					<th></th>
					{this.headings.map((heading, index) => (<th key={index}>{heading}</th>))}
				</tr>
			</thead>
			<tbody>
				{this.data.map((point, index) => (<tr key={index}>
						<td>
							<span className={styles.swatch} style={{ backgroundColor: point.color || MISSING_COLOUR_FALLBACK }}/>
						</td>
						{point.columns.map((value, index) => (<td key={index}>{value}</td>))}
					</tr>))}
			</tbody>
		</table>
	</>;
        this.headings = opts.headings;
        this.data = opts.data;
        this.options = merge(DEFAULT_CHART_OPTIONS, opts.options);
    }
    get chartData() {
        const dataset = this.data.reduce((carry, point) => {
            carry.data.push(point.value);
            carry.backgroundColor.push(point.color || MISSING_COLOUR_FALLBACK);
            return carry;
        }, { data: [], backgroundColor: [] });
        return {
            datasets: [dataset],
        };
    }
}
