import classNames from 'classnames';
import React from 'react';
import styles from './List.module.css';
class ListItem extends React.PureComponent {
    render() {
        const { as: Component = 'div', children, ...props } = this.props;
        return (<Component {...props} className={classNames(styles.listItem, props.className)}>
				{children}
			</Component>);
    }
}
export class List extends React.PureComponent {
    render() {
        const { color, children } = this.props;
        return (<div className={styles.list} style={{ borderColor: color }}>
				{children}
			</div>);
    }
}
List.Item = ListItem;
